.lottiKarottiWrapper {
    margin: 0 auto;
    height: 100vh;
    max-width: 100%;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.header {
    color: #f05408;
    font-size: 1.5em;
    text-align: center;
    max-height: 150px;
    z-index: 1;
}

.header h1 {
    transform: rotate(-5deg);
}

.buttonWrapper {
    display: flex;
    justify-content: center;
}

.karottiButton {
    border: 2px solid #f05408;
    background-color: #f05408;
    color: rgba(0, 0, 10, 0.95);
    margin: 15px;
    height: 60px;
    width: 50%;
    font-size: 1.2em;
    border-radius: 4px;
    font-weight: 900;
}