.cards {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}

.carrotImg {
    width: 100px;
    transform: rotate(15deg);
    position: relative;
    top: -300px;
    right: -150px;
}

.rabbitImg {
    width: 200px;
    transform: rotate(-15deg);
    position: relative;
    left: -130px;
    top: -240px;
}

.cards h2 {
    position: absolute;
    width: 200px;
    z-index: 1;
    text-align: center;
}

.cardStack {
    width: 310px;
    height: 445px;
    border: 1px solid #f05408;
    position: absolute;
    border-radius: 10px;
}

.cardBack {
    width: 100%;
    height: 100%;
    background-color: cornflowerblue;
    border-radius: 10px;
    position: absolute;
    box-shadow: 0 4px 6px 0 hsla(0, 0%, 0%, 0.9);
}

.cardBack:nth-child(1) {
    transform: rotate(-5deg);
}

.cardBack:nth-child(2) {
    transform: rotate(5deg);
}

.topCard {
    width: 300px;
    border-radius: 10px;
    box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034);
    z-index: 2;
    box-shadow: 0 4px 6px 0 hsla(0, 0%, 0%, 0.9);
    position: relative;
}